import { BACK_URL } from './../constants';
import axios from 'axios';
import {
	BuildingObj,
	DptoObj,
	GarageObj,
	ServiceObj,
	TypologyObj
} from '../types/types';

export interface CommonResponse<T> {
	ok: boolean;
	content: T;
}

export interface BuildingsResponse extends BuildingObj {
	dptos: Array<DptoObj & { typology: TypologyObj }>;
	services: Array<ServiceObj>;
	// typologies: Array<TypologyObj>;
	garages: Array<GarageObj>;
}

export const getBuildings = async () => {
	const response = await axios.get(`${BACK_URL}/building`);
	return response.data.content as Array<BuildingsResponse>;
};

export const getBuildingById = async (building_id: number) => {
	const response = await axios.get(`${BACK_URL}/building/${building_id}`);
	return response.data as CommonResponse<BuildingsResponse>;
};

export const getBuildingWithDptos = async (buildingId: number) => {
	const response = await axios.get(`${BACK_URL}/building/${buildingId}/dptos`);
	return response.data as CommonResponse<
		BuildingObj & {
			dptos: Array<
				Pick<DptoObj, 'building_id' | 'id' | 'number' | 'obs'> & {
					services: Array<ServiceObj>;
				}
			>;
			services: Array<ServiceObj>;
		}
	>;
};

export const postBuildingCreate = async (
	building: BuildingObj,
	services?: Array<number>
) => {
	const response = await axios.post(`${BACK_URL}/building`, {
		...building,
		services
	});
	return response.data;
};
