import { HashRouter, Route, Routes } from 'react-router-dom';
import { routes } from './routes';
import { ToastContainer } from 'react-toastify';

const Navigation = () => {
	return (
		<HashRouter>
			<ToastContainer
				position="top-right"
				autoClose={4000}
				hideProgressBar={false}
				newestOnTop={false}
				closeOnClick
				rtl={false}
				pauseOnFocusLoss
				draggable
				pauseOnHover
			/>
			<Routes>
				{routes.map(({ component: Component, path, layout: Layout }, idx) => (
					<Route
						key={idx}
						path={path}
						element={
							<Layout>
								<Component />
							</Layout>
						}
					/>
				))}
				<Route path="*" element={<div>not found</div>} />
			</Routes>
		</HashRouter>
	);
};

export default Navigation;
