import React, { useContext, useMemo } from 'react';
import NumberFormat from 'react-number-format';
import { FormContext } from '../context/QuoteRegisterContext';

const FormPrices = () => {
	const formContext = useContext(FormContext);
	const {
		modePDF,
		showGarages,
		selectedDpto,
		selectedGarage,
		discount,
		setDiscount,
		initialCash,
		setInitialCash
	} = formContext!;

	const totalAmount = useMemo(() => {
		if (showGarages) {
			return (
				(selectedDpto?.price || 0) + (selectedGarage?.price || 0) - discount
			);
		} else {
			return (selectedDpto?.price || 0) - discount;
		}
	}, [selectedDpto, selectedGarage, showGarages, discount]);

	return (
		<table className="table table-bordered table-sm">
			{modePDF && (
				<thead>
					<tr>
						<th colSpan={2} className="text-center">
							PRECIO
						</th>
					</tr>
				</thead>
			)}
			<tbody>
				<tr>
					<td className={'text-end fw-bold'}>Precio Dpto:</td>
					<td>
						<NumberFormat
							value={selectedDpto?.price}
							displayType={'text'}
							thousandSeparator={true}
							prefix={'S/ '}
							className="form-control text-center border-0 bg-transparent"
						/>
					</td>
				</tr>
				{showGarages && (
					<tr>
						<td className={'text-end fw-bold'}>Precio Cochera:</td>
						<td>
							<NumberFormat
								value={showGarages ? selectedGarage?.price : 0}
								displayType={'text'}
								thousandSeparator={true}
								prefix={'S/ '}
								className="form-control text-center border-0 bg-transparent"
							/>
						</td>
					</tr>
				)}

				<tr>
					<td className={'text-end fw-bold'}>Descuento promocional:</td>
					<td>
						<NumberFormat
							disabled={modePDF}
							value={discount}
							displayType={'input'}
							thousandSeparator={true}
							onValueChange={({ value }) => {
								if (!value) {
									setDiscount(0);
								} else {
									setDiscount(+value);
								}
							}}
							prefix={'S/ - ('}
							suffix={')'}
							className={`${
								modePDF ? 'bg-transparent border-0' : null
							} form-control text-danger text-center`}
						/>
					</td>
				</tr>

				<tr>
					<td className="text-end fw-bold">Cuota Inicial:</td>
					<td>
						<NumberFormat
							disabled={modePDF}
							readOnly={modePDF}
							value={initialCash}
							displayType={'input'}
							thousandSeparator={true}
							onValueChange={({ value }) => {
								if (!value) {
									setInitialCash(0);
								} else {
									setInitialCash(+value);
								}
							}}
							prefix={'S/ '}
							className={`${
								modePDF ? 'bg-transparent border-0 ' : ' '
							}fw-bold p-0 form-control text-center text-primary`}
						/>
					</td>
				</tr>
				<tr>
					<td className="text-end fw-bold">Total:</td>
					<td>
						<NumberFormat
							value={totalAmount}
							displayType={'text'}
							thousandSeparator={true}
							prefix={'S/ '}
							className="fw-bold fs-5 form-control text-center bg-transparent border-0 text-success"
						/>
					</td>
				</tr>
			</tbody>
		</table>
	);
};

export default FormPrices;
