import { CommonResponse } from './buildingservice';
import { BACK_URL } from './../constants';
import axios from 'axios';

export const getPersonByDni = async (dni: string) => {
	const url = `${BACK_URL}/reniec/get-person-by-dni/${dni}`;
	const response = await axios(url);
	return response.data as CommonResponse<string>;
};
