import React, { useContext, useMemo, useState } from 'react';
import { BuildingsResponse } from '../../services/buildingservice';
import GenericModal from '../../shared/components/GenericModal';
import { FormContext } from '../context/QuoteRegisterContext';

interface FormStep1DptoInfoProps {
	buildings: BuildingsResponse[];
	SW: any;
}

const FormStep1DptoInfo = (props: FormStep1DptoInfoProps) => {
	const { buildings, SW } = props;

	const formContext = useContext(FormContext);
	const {
		isCash,
		setIsCash,
		setSelectedBuilding,
		selectedBuilding,
		selectedDpto,
		setSelectedDpto,
		selectedGarage,
		setSelectedGarage,
		showGarages,
		setShowGarages
	} = formContext!;
	const [showImageModal, setShowImageModal] = useState(false);
	const [showModalGarage, setShowModalGarage] = useState(false);

	const handleChangeBuilding = (e: React.ChangeEvent<HTMLSelectElement>) => {
		const { value: id } = e.target;
		const buildingFound = buildings.find((b) => b.id === +id);
		setSelectedBuilding(buildingFound!);
	};

	const handleChangeDpto = (e: React.ChangeEvent<HTMLSelectElement>) => {
		const { value: id } = e.target;
		const dptoFound = selectedBuilding!.dptos.find((d) => d.id === +id);

		setSelectedDpto({ ...dptoFound! });
	};

	const handleChangeGarage = (e: React.ChangeEvent<HTMLSelectElement>) => {
		const { value: id } = e.target;
		const garageFound = selectedBuilding!.garages.find((g) => g.id === +id);
		setSelectedGarage(garageFound!);
	};

	const availableDptos = useMemo(() => {
		return (
			selectedBuilding?.dptos.map((d) => {
				return {
					label: `${d.number} / ${d.typology?.name} / (${d.typology?.area} mts)`,
					value: d.id
				};
			}) || []
		);
	}, [selectedBuilding]);

	const availableBuildings = buildings.map((b) => {
		return {
			label: b.name,
			value: b.id
		};
	});

	const availableGarages = useMemo(() => {
		return (
			selectedBuilding?.garages.map((g) => {
				return {
					label: `${g.name} / S/ (${g.price.toFixed(2)})`,
					value: g.id
				};
			}) || []
		);
	}, [selectedBuilding]);

	return (
		<>
			<GenericModal
				show={showImageModal}
				setShow={setShowImageModal}
				title={selectedDpto?.typology?.name || ''}
			>
				<div className="row">
					<div className="col text-center">
						<img
							src={
								selectedDpto?.typology?.url ||
								'https://via.placeholder.com/250?text=no%20disponible'
							}
							alt=""
							style={{ maxWidth: '100%' }}
						/>
					</div>
				</div>
			</GenericModal>

			<GenericModal
				show={showModalGarage}
				setShow={setShowModalGarage}
				title={selectedGarage?.name || ''}
			>
				<div className="row">
					<div className="col text-center">
						<img
							src={
								selectedGarage?.url ||
								'https://via.placeholder.com/250?text=no%20disponible'
							}
							alt=""
							style={{ maxWidth: '100%' }}
						/>
					</div>
				</div>
			</GenericModal>

			<div className="row mb-3">
				<div className="col-md-6">
					<label htmlFor="buildings" className="form-label">
						Seleccione Proyecto:
					</label>
					<select
						name="buildings"
						id="buildings"
						className="form-control"
						placeholder="Proyectos disponibles"
						onChange={handleChangeBuilding}
						value={selectedBuilding?.id || 0}
					>
						<option value={0}>Seleccione</option>
						{availableBuildings.map((b) => {
							return (
								<option value={b.value} key={b.value}>
									{b.label}
								</option>
							);
						})}
					</select>
				</div>
				<div className="col-md-6">
					<label htmlFor="dptos" className="form-label">
						Seleccione el Dpto:
					</label>
					<select
						name="dptos"
						id="dptos"
						className="form-control"
						placeholder="Departamentos disponibles"
						onChange={handleChangeDpto}
						value={selectedDpto?.id || 0}
					>
						<option value={0}>Seleccione</option>
						{availableDptos.map((d) => {
							return (
								<option value={d.value} key={d.value}>
									{d.label}
								</option>
							);
						})}
					</select>
				</div>
			</div>

			<div className="row mb-3">
				<div className="col-md-6 d-flex flex-column justify-content-center">
					{showGarages && (
						<div>
							<label htmlFor="garages" className="form-label">
								Seleccione Garage:
							</label>
							<select
								name="garages"
								id="garages"
								className="form-control"
								placeholder="Garajes disponibles"
								onChange={handleChangeGarage}
								value={selectedGarage?.id || 0}
							>
								<option value={0}>Seleccione</option>
								{availableGarages.map((g) => {
									return (
										<option value={g.value} key={g.value}>
											{g.label}
										</option>
									);
								})}
							</select>
						</div>
					)}
				</div>
				<div className="col-md-3 col-sm-6 d-flex flex-column justify-content-center align-items-start">
					<div className="form-check">
						<input
							className="form-check-input"
							type="radio"
							name="is-cash"
							id="radio-cash"
							checked={isCash}
							onChange={(e) => setIsCash(e.target.checked)}
						/>
						<label className="form-check-label" htmlFor="radio-cash">
							Pago al contado
						</label>
					</div>
					<div className="form-check">
						<input
							className="form-check-input"
							type="radio"
							name="is-cash"
							id="radio-non-cash"
							checked={!isCash}
							onChange={(e) => setIsCash(!e.target.checked)}
						/>
						<label className="form-check-label" htmlFor="radio-non-cash">
							Crdito hipotecario
						</label>
					</div>
				</div>
				<div className="col-md-3 col-sm-6 d-flex flex-column justify-content-center">
					<div className="form-check form-switch">
						<label className="form-check-label" htmlFor="show-garage">
							Incluir garage
						</label>
						<input
							className="form-check-input"
							type="checkbox"
							role="switch"
							id="show-garage"
							checked={showGarages}
							onChange={() => setShowGarages(!showGarages)}
						/>
					</div>
				</div>
			</div>
			<div className="row">
				<div className="col-md-6 text-center">
					{selectedGarage && showGarages ? (
						<table className="table">
							<thead>
								<tr>
									<th>Garage</th>
								</tr>
							</thead>
							<tbody>
								<tr>
									<td>
										<img
											style={{ maxWidth: '150px', maxHeight: '150px' }}
											className="img-thumbnail cursor-pointer"
											src={
												selectedGarage?.url ||
												'https://via.placeholder.com/250?text=no%20disponible'
											}
											alt="typology image thumbnail"
											onClick={() => setShowModalGarage(true)}
										/>
									</td>
								</tr>
							</tbody>
						</table>
					) : null}
				</div>
				<div className="col-md-6 text-center">
					<table className="table">
						<thead>
							<tr>
								<th>Tipologa</th>
							</tr>
						</thead>
						<tbody>
							<tr>
								<td>
									<img
										style={{ maxWidth: '150px', maxHeight: '150px' }}
										className="img-thumbnail cursor-pointer"
										src={
											selectedDpto?.typology?.url ||
											'https://via.placeholder.com/250?text=no%20disponible'
										}
										alt="typology image thumbnail"
										onClick={() => setShowImageModal(true)}
									/>
								</td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>

			<div className="row">
				<div className="col text-end">
					<button className="btn btn-primary" onClick={() => SW.nextStep()}>
						Siguiente <i className="fa-solid fa-angle-right"></i>
					</button>
				</div>
			</div>
		</>
	);
};

export default FormStep1DptoInfo;
