import React, { useMemo, useState } from 'react';
import { BuildingObj, DptoObj, ServiceObj } from '../../types/types';

interface CalculatorProps {
	building: BuildingObj & {
		dptos: Array<
			Pick<DptoObj, 'building_id' | 'id' | 'number' | 'obs'> & {
				services: Array<ServiceObj>;
			}
		>;
		services: Array<ServiceObj>;
	};
}

const Caltulator = ({ building }: CalculatorProps) => {
	const { dptos, services: availableServices } = building;
	const [monto, setMonto] = useState<number>(0);

	const totalServices = useMemo(() => {
		let sum = 0;
		for (let i = 0; i < dptos.length; i++) {
			sum += dptos[i].services.length;
		}
		return sum;
	}, [building, dptos]);

	return (
		<>
			<div className="row justify-content-center">
				<div className="col-md-6">
					<div className="card shadow">
						<div className="card-body">
							<form
								noValidate
								action=""
								autoComplete="off"
								onSubmit={(e) => {
									e.preventDefault();
								}}
							>
								<div className="mb-3">
									<label htmlFor="monto" className="form-label fs-4">
										Ingrese el monto del recibo:
									</label>
									<input
										id="monto"
										className="form-control form-control-lg text-center"
										type="number"
										value={monto}
										onChange={(e) => setMonto(parseFloat(e.target.value))}
										placeholder="Ejm: 125.50"
									/>
								</div>
								<div className="mb-3">
									<button className="w-100 btn btn-primary btn-lg">
										<i className="fa-solid fa-calculator"></i> Calcular
									</button>
								</div>
							</form>
						</div>
					</div>
				</div>
			</div>
			<div className="row mt-3">
				<div className="col">
					<div className="card shadow">
						<div className="card-body">
							<div className="table-responsive">
								<table className="table table-bordered text-center table-sm">
									<tbody>
										<tr>
											<th scope="col" rowSpan={2}>
												#
											</th>
											<th scope="col" rowSpan={2}>
												Departamento
											</th>
											<th scope="col" rowSpan={2}>
												Pagar
											</th>
											<th
												scope="col"
												rowSpan={1}
												colSpan={availableServices.length}
											>
												Servicios
											</th>
											<th scope="col" rowSpan={2}>
												Observaciones
											</th>
										</tr>
										<tr>
											{availableServices.map((serv, idx) => {
												return <th key={idx}>{serv.name}</th>;
											})}
										</tr>

										{dptos.map((dpto, idx) => {
											return (
												<tr key={idx}>
													<th scope="row">{idx + 1}</th>
													<td>{dpto.number}</td>
													<td>
														{monto
															? `S/ ${(
																	(monto / totalServices) *
																	dpto.services.length
															  ).toFixed(2)}`
															: `S/0.00`}
													</td>
													{availableServices.map((serv, idx) => {
														const serviceFound = dpto.services.find(
															(currS) => currS.id === serv.id
														);
														if (serviceFound) {
															return (
																<td key={idx}>
																	<i className="fa-solid fa-circle-check text-success"></i>
																</td>
															);
														} else {
															return (
																<td key={idx}>
																	<i className="fa-solid fa-circle-xmark text-danger"></i>
																</td>
															);
														}
													})}
													<td>{dpto.obs}</td>
												</tr>
											);
										})}
									</tbody>
								</table>
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
};

export default Caltulator;
