import React, { useState, useEffect } from 'react';
import { Navigate } from 'react-router-dom';
import { routesCollection } from '../../routes/routes';
import LoadingComponent from '../../shared/components/LoadingComponent';
import { AuthContextQuote } from './context';
import { getToken, removeToken } from '../../utils/token';
import { postVerifySession } from '../../services/userservice';
import { UserObj } from '../../types/types';

export interface IAuthStateQuoteProps {
	children?: React.ReactNode;
}

const AuthStateQuote = (props: IAuthStateQuoteProps) => {
	// TODO aqu se hace el logeo
	const [loadingLogin, setLoadingLogin] = useState(true);
	const [autenticated, setAutenticated] = useState(false);
	const [currentUser, setCurrentUser] = useState<
		Pick<UserObj, 'id' | 'fullname' | 'phone'> | undefined
	>();

	const verifySessionCall = async () => {
		const token = getToken();
		if (token) {
			setLoadingLogin(true);
			try {
				const response = await postVerifySession(token);
				if (response.ok) {
					setCurrentUser({
						id: response.content.id,
						fullname: response.content.fullname,
						phone: response.content.phone
					});
					setAutenticated(true);
				} else {
					setAutenticated(false);
					setCurrentUser(undefined);
				}
			} catch (error: any) {
				setAutenticated(false);
				setCurrentUser(undefined);
			}
		} else {
			setAutenticated(false);
			setCurrentUser(undefined);
		}
		setLoadingLogin(false);
	};

	const endQuoteSession = () => {
		setAutenticated(false);
		setCurrentUser(undefined);
		removeToken();
	};

	useEffect(() => {
		verifySessionCall();
	}, []);

	return loadingLogin ? (
		<LoadingComponent title="Verificando credenciales" message="" />
	) : (
		<AuthContextQuote.Provider
			value={{ autenticated, currentUser, endQuoteSession }}
		>
			{autenticated ? (
				props.children
			) : (
				<Navigate to={routesCollection.QUOTE_LOGIN} />
			)}
		</AuthContextQuote.Provider>
	);
};

export default AuthStateQuote;
