import { AxiosError } from 'axios';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { getBuildingWithDptos } from '../../services/buildingservice';
import LoadingComponent from '../../shared/components/LoadingComponent';
import { BuildingObj, DptoObj, ServiceObj } from '../../types/types';
import Caltulator from '../components/Caltulator';

const PageCalc = () => {
	const params = useParams();
	const { building_id } = params;
	const [loadingBuilding, setLoadingBuilding] = useState(true);
	const [errorRe, setErrorRe] = useState(false);
	const [building, setBuilding] = useState<
		BuildingObj & {
			dptos: Array<
				Pick<DptoObj, 'building_id' | 'id' | 'number' | 'obs'> & {
					services: Array<ServiceObj>;
				}
			>;
			services: Array<ServiceObj>;
		}
	>();
	useEffect(() => {
		getBuildingWithDptos(+building_id!)
			.then((data) => {
				if (data.ok) {
					setBuilding(data.content);
					setLoadingBuilding(false);
				} else {
					setErrorRe(true);
					setLoadingBuilding(false);
				}
			})
			.catch((error: AxiosError) => {
				setErrorRe(true);
				setLoadingBuilding(false);
			});
	}, [building_id]);

	return (
		<>
			{errorRe && (
				<div className="row calc-container">
					<div className="col">
						<div className="alert alert-danger" role="alert">
							Ups! parece que la URL de la pgina que intentas visitar no es
							correcta. Contacte al administrador
						</div>
					</div>
				</div>
			)}
			<div className="row calc-container">
				<div className="col">
					{loadingBuilding ? (
						<LoadingComponent
							className="mt-3 alert alert-warning"
							title="Cargando la informacin del edificio"
							message="Estamos obteniendo la informacin del servidor"
						/>
					) : !errorRe ? (
						<div className="display-6 text-center">
							Calculadora de servicios - <strong>{building?.name}</strong>
						</div>
					) : null}
				</div>
			</div>
			{!loadingBuilding && !errorRe ? (
				<div className="row">
					<div className="col pt-4">
						<Caltulator building={building!} />
					</div>
				</div>
			) : null}
		</>
	);
};

export default PageCalc;
