import { UserObj } from './../types/types';
import { BACK_URL } from './../constants';
import axios from 'axios';
import { CommonResponse } from './buildingservice';

export const getUsers = async () => {
	const response = await axios.get(`${BACK_URL}/user`);
	return response.data as CommonResponse<UserObj[]>;
};

export const postLogin = async (loginObj: { id: number; password: string }) => {
	const response = await axios.post(`${BACK_URL}/user/login`, { ...loginObj });
	return response.data as CommonResponse<string>;
};

export const postVerifySession = async (token: string) => {
	const response = await axios.post(
		`${BACK_URL}/user/verify-session`,
		{},
		{ headers: { Authorization: `Bearer ${token}` } }
	);
	return response.data as CommonResponse<
		Pick<UserObj, 'id' | 'fullname' | 'phone'>
	>;
};
