import { months } from './constants';

export const capitalize = (word: string) => {
	word = word.toLowerCase();
	return word.charAt(0).toUpperCase() + word.substring(1);
};

export const getFormatedMonth = (date: Date): string => {
	return `${date.getDate()}-${months
		.at(date.getMonth())
		?.substring(0, 3)}-${date.getFullYear()}`;
};

export const getFormatedDateByStringDate = (dateString: string): string => {
	const [year, month, day] = dateString.split('T')[0].split('-');
	return `${day}-${months.at(+month - 1)?.substring(0, 3)}-${year}`;
};
