import React, { useEffect, useState } from 'react';
import {
	BuildingsResponse,
	getBuildings
} from '../../services/buildingservice';
import LoadingComponent from '../../shared/components/LoadingComponent';
import FormQuoteRegister from '../components/FormQuoteRegister';

const PageQuoteRegister = () => {
	const [buildings, setBuildings] = useState<BuildingsResponse[]>([]);
	const [loadingData, setLoadingData] = useState(true);
	const getBuildingsCall = () => {
		setLoadingData(true);
		getBuildings()
			.then((data) => {
				setBuildings(data);
				setLoadingData(false);
			})
			.catch((error: any) => {
				setLoadingData(false);
			});
	};

	useEffect(() => {
		getBuildingsCall();
	}, []);

	return (
		<>
			<div className="container">
				<div className="row mb-3">
					<div className="col">
						<h1 className="display-6">Cotizaciones</h1>
					</div>
				</div>
				<div className="row">
					<div className="col">
						{loadingData ? (
							<LoadingComponent
								title="Cargando proyectos"
								message="Estamos conectndonos a la base de datos de Angeles"
							/>
						) : (
							<FormQuoteRegister buildings={buildings} />
						)}
					</div>
				</div>
			</div>
		</>
	);
};

export default PageQuoteRegister;
