import React, { useEffect, useState } from 'react';
import DataTable, { TableColumn } from 'react-data-table-component';
import { useNavigate } from 'react-router-dom';
import { getQuotes, QuotesResponse } from '../../services/quoteservice';
import LoadingComponent from '../../shared/components/LoadingComponent';
import { RoutesCollection } from '../../routes/routes';

const FilterComponent = ({
	filterText,
	onFilter,
	onClear
}: {
	filterText: string;
	onFilter: (e: any) => void;
	onClear: () => void;
}) => (
	<div className="row">
		<div className="col-md-6"></div>
		<div className="col-md-6">
			<div className="input-group">
				<input
					id="search"
					className="form-control"
					type="text"
					placeholder="Filter By Name"
					aria-label="Search Input"
					value={filterText}
					onChange={onFilter}
				/>
				<span className="input-group-text" onClick={onClear}>
					X
				</span>
			</div>
		</div>
	</div>
);
const PageQuoteList = () => {
	const [quotes, setQuotes] = useState<QuotesResponse[]>();
	const [loadingQuotes, setLoadingQuotes] = useState(true);
	const navigate = useNavigate();
	const getQuotesCall = async () => {
		try {
			setLoadingQuotes(true);
			const response = await getQuotes();
			if (response.ok) {
				const orderedQuotes = response.content.sort(
					(obj1, obj2) => obj2.id! - obj1.id!
				);
				setQuotes(orderedQuotes);
			}
			setLoadingQuotes(false);
		} catch (error) {}
	};
	useEffect(() => {
		getQuotesCall();
	}, []);

	const columns: TableColumn<QuotesResponse>[] = [
		{
			name: 'Vendedor',
			sortable: true,
			selector: (row: QuotesResponse) => row.user.fullname || ''
		},
		{
			name: 'Cliente',
			sortable: true,
			selector: (row: QuotesResponse) => row.customer.fullname || ''
		},
		{
			name: 'Proyecto',
			selector: (row: QuotesResponse) => row.dpto.building.name
		},
		{
			name: 'Dpto',
			selector: (row: QuotesResponse) => row.dpto.number
		},
		{
			name: 'Tipologa',
			selector: (row: QuotesResponse) => row.dpto.typology.name
		},
		{
			name: 'Piso',
			selector: (row: QuotesResponse) => row.dpto.floor
		},
		{
			sortable: true,
			name: 'Fecha de cotizacin',
			selector: (row: QuotesResponse) => row.date.toString().split('T')[0]
		},
		{
			name: 'Acciones',

			cell: (row: QuotesResponse) => (
				<button
					className="btn btn-primary"
					onClick={() =>
						navigate(RoutesCollection.goToQuoteSummary({ quote_id: row.id! }))
					}
				>
					Ver cotizacin
				</button>
			),
			button: true
		}
	];

	const [filterText, setFilterText] = React.useState('');
	const [resetPaginationToggle, setResetPaginationToggle] =
		React.useState(false);
	const filteredItems = quotes
		? quotes.filter(
				(item) =>
					item.customer.fullname &&
					item.customer.fullname
						.toLowerCase()
						.includes(filterText.toLowerCase())
		  )
		: [];

	const subHeaderComponentMemo = React.useMemo(() => {
		const handleClear = () => {
			if (filterText) {
				setResetPaginationToggle(!resetPaginationToggle);
				setFilterText('');
			}
		};

		return (
			<FilterComponent
				onFilter={(e) => setFilterText(e.target.value)}
				onClear={handleClear}
				filterText={filterText}
			/>
		);
	}, [filterText, resetPaginationToggle]);

	return (
		<>
			<div className="row">
				<div className="col">
					<h1 className="display-6">Lista de cotizaciones</h1>
				</div>
			</div>
			<div className="row mt-3">
				<div className="col">
					<div className="card shadow">
						<div className="card-body">
							{loadingQuotes ? (
								<LoadingComponent
									title="Cargando cotizaciones"
									message="Conectndose a la base de datos de ngeles"
								/>
							) : (
								<DataTable
									title="Cotizaciones"
									pagination
									columns={columns}
									data={filteredItems!}
									subHeader
									subHeaderComponent={subHeaderComponentMemo}
									persistTableHead
								/>
							)}
						</div>
					</div>
				</div>
			</div>
		</>
	);
};

export default PageQuoteList;
