import React, { useContext } from 'react';
import { FormContext } from '../context/QuoteRegisterContext';

const FormResume = () => {
	const formContext = useContext(FormContext);
	const { selectedDpto, selectedGarage, showGarages } = formContext!;

	return (
		<>
			<div className="row mb-3">
				<div className={showGarages ? 'col-md-4' : 'col-md-6'}>
					<label htmlFor="typologies" className="form-label">
						Tipologa:
					</label>
					<input
						disabled
						name="typology"
						id="typology"
						className="form-control form-control-lg"
						type="text"
						defaultValue={selectedDpto?.typology?.name}
					/>
				</div>
				{showGarages && (
					<div className="col-md-4">
						<label htmlFor="typologies" className="form-label">
							Garages:
						</label>
						<input
							disabled
							readOnly
							name="typology"
							id="typology"
							className="form-control form-control-lg"
							type="text"
							onChange={() => {}}
							value={selectedGarage?.name || ''}
						/>
					</div>
				)}
				<div className={showGarages ? 'col-md-4' : 'col-md-6'}>
					<label htmlFor="floors" className="form-label">
						Nivel:
					</label>
					<input
						disabled
						name="floor"
						id="floor"
						className="form-control form-control-lg"
						type="text"
						defaultValue={selectedDpto?.floor}
					/>
				</div>
			</div>
		</>
	);
};

export default FormResume;
