import React, { useMemo, useState } from 'react';
import { BuildingObj, DptoObj, ServiceObj } from '../../types/types';

interface CalculatorProps {
	building: BuildingObj & {
		Dptos: Array<
			Pick<DptoObj, 'building_id' | 'id' | 'number' | 'obs'> & {
				Services: Array<ServiceObj>;
			}
		>;
		Services: Array<ServiceObj>;
	};
}

const CaltulatorBolo = ({ building }: CalculatorProps) => {
	const { Dptos: dptos, Services: availableServices } = building;
	const [monto, setMonto] = useState<number>(0);

	const dptosWithWeight = useMemo(() => {
		return dptos.map((d) => {
			const w = d.Services.find((s) =>
				s.name.toLowerCase().includes('ascensor')
			)
				? d.Services.length + 1
				: d.Services.length;

			return { ...d, weight: w };
		});
	}, [building, dptos]);

	const totalServices = useMemo(() => {
		let sum = 0;
		for (let i = 0; i < dptosWithWeight.length; i++) {
			sum += dptosWithWeight[i].weight;
		}
		return sum;
	}, [building, dptosWithWeight]);
	return (
		<>
			<div className="row justify-content-center">
				<div className="col-md-6">
					<div className="card shadow">
						<div className="card-body">
							<form
								noValidate
								autoComplete="off"
								onSubmit={(e) => {
									e.preventDefault();
								}}
							>
								<div className="mb-3">
									<label htmlFor="monto" className="form-label fs-4">
										Ingrese el monto del recibo:
									</label>
									<input
										id="monto"
										className="form-control form-control-lg"
										type="number"
										value={monto ? monto : ''}
										onChange={(e) => {
											if (e.target.value.trim() === '') {
												setMonto(0);
											} else {
												setMonto(+parseFloat(e.target.value).toFixed(2));
											}
										}}
										placeholder="Ejm: 125.50"
									/>
								</div>
								<div className="mb-3">
									<button className="w-100 btn btn-primary btn-lg">
										<i className="fa-solid fa-calculator"></i> Calcular
									</button>
								</div>
							</form>
						</div>
					</div>
				</div>
			</div>
			<div className="row mt-3">
				<div className="col">
					<div className="card shadow">
						<div className="card-body">
							<div className="table-responsive">
								<table className="table table-bordered text-center">
									<thead>
										<tr>
											<th scope="col" rowSpan={2}>
												Dpto.
											</th>
											<th scope="col" rowSpan={2}>
												Pagar
											</th>
											<th
												scope="col"
												rowSpan={1}
												colSpan={availableServices.length}
											>
												Servicios
											</th>
											<th scope="col" rowSpan={2}>
												Observaciones
											</th>
										</tr>
										<tr>
											{availableServices.map((serv, idx) => {
												return <th key={idx}>{serv.name}</th>;
											})}
										</tr>
									</thead>

									<tbody className="table-group-divider">
										{dptosWithWeight.map((dpto, idx) => {
											return (
												<tr key={idx}>
													<td className="col-dpto">{dpto.number}</td>
													<td className="table-warning col-pago">
														<strong>
															{monto
																? `S/ ${(
																		(monto / totalServices) *
																		dpto.weight
																  ).toFixed(2)}`
																: `S/0.00`}
														</strong>
													</td>
													{availableServices.map((serv, idx) => {
														const serviceFound = dpto.Services.find(
															(currS) => currS.id === serv.id
														);
														if (serviceFound) {
															return (
																<td key={idx}>
																	<i className="fa-solid fa-circle-check text-success"></i>
																</td>
															);
														} else {
															return (
																<td key={idx}>
																	<i className="fa-solid fa-circle-xmark text-danger"></i>
																</td>
															);
														}
													})}
													<td>{dpto.obs}</td>
												</tr>
											);
										})}
									</tbody>
								</table>
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
};

export default CaltulatorBolo;
