import React, { useContext, useMemo } from 'react';
import { getFormatedMonth } from '../../utils';
import NumberFormat from 'react-number-format';
import { FormContext } from '../context/QuoteRegisterContext';

const FormPaymentList = () => {
	const classInputCurrency =
		'form-control form-control-lg text-center bg-transparent border-0';

	const formContext = useContext(FormContext);
	const {
		initialCashDate,
		finalCashDate,
		discount,
		showGarages,
		setInitialCashDate,
		setFinalCashDate,
		initialCash,
		setInitialCash,
		paymentsQty,
		setPaymentsQty,
		modePDF,
		selectedDpto,
		isCash,
		selectedGarage
	} = formContext!;

	const totalAmount = useMemo(() => {
		if (showGarages) {
			return (
				(selectedDpto?.price || 0) + (selectedGarage?.price || 0) - discount
			);
		} else {
			return (selectedDpto?.price || 0) - discount;
		}
	}, [selectedDpto, selectedGarage, discount, paymentsQty, showGarages]);

	const paymentPerMonth = useMemo(() => {
		return ((totalAmount * 0.9 - initialCash) / (paymentsQty + 1)).toFixed(2);
	}, [
		selectedDpto,
		selectedGarage,
		discount,
		initialCash,
		paymentsQty,
		showGarages
	]);

	const paymentsDates = useMemo(() => {
		const [yearInit, monthInit, dayInit] = initialCashDate.split('-');
		const [yearFinish, monthFinish, dayFinish] = finalCashDate.split('-');
		const dateInit = new Date(+yearInit, +monthInit - 1, +dayInit);
		const dateFinish = new Date(+yearFinish, +monthFinish - 1, +dayFinish);
		const diffUTC = dateFinish.valueOf() - dateInit.valueOf();
		const eachMonth = diffUTC / (paymentsQty + 1);
		return Array(paymentsQty)
			.fill('')
			.map((it, index) => {
				const tmpDate = new Date(dateInit.valueOf() + eachMonth * (index + 1));
				return getFormatedMonth(tmpDate);
			});
	}, [initialCashDate, finalCashDate, paymentsQty]);

	return (
		<>
			{!modePDF && (
				<div className="row mb-3">
					<div className="col-6">
						<button
							disabled={paymentsQty === 0}
							className="btn btn-outline-danger w-100"
							onClick={() => setPaymentsQty(paymentsQty - 1)}
						>
							<i className="fa-solid fa-minus fa-2x"></i>
						</button>
					</div>
					<div className="col-6">
						<button
							className="btn btn-outline-primary w-100"
							onClick={() => setPaymentsQty(paymentsQty + 1)}
						>
							<i className="fa-solid fa-circle-plus fa-2x"></i>
						</button>
					</div>
				</div>
			)}
			<div className="row d-flex align-items-start ">
				<div className="table-responsive col">
					<table className="table table-bordered table-sm">
						{modePDF && (
							<thead>
								<tr>
									<th className="text-center" colSpan={4}>
										FORMA DE PAGO
									</th>
								</tr>
							</thead>
						)}
						<tbody>
							<tr className="text-center">
								<th></th>
								<th>Porcentaje</th>
								<th>Monto</th>
								<th>Fechas de pago</th>
							</tr>
							<tr>
								<td className="text-end">
									<strong>Cuota Inicial:</strong>
								</td>
								<td align="center">
									<NumberFormat
										value={((initialCash * 100) / totalAmount).toFixed(2)}
										displayType={'text'}
										suffix={' %'}
									/>
								</td>
								<td>
									<NumberFormat
										disabled={modePDF}
										readOnly={modePDF}
										value={initialCash}
										displayType={'input'}
										thousandSeparator={true}
										onValueChange={({ value }) => {
											if (!value) {
												setInitialCash(0);
											} else {
												setInitialCash(+value);
											}
										}}
										prefix={'S/ '}
										className={`${
											modePDF ? 'bg-transparent border-0 ' : ' '
										} fw-bold p-0 form-control text-center text-primary`}
									/>
								</td>
								<td align="center">
									{modePDF ? (
										!isCash ? (
											<span>Por definir</span>
										) : (
											<span>{initialCashDate}</span>
										)
									) : (
										<input
											type="date"
											onChange={(e) => setInitialCashDate(e.target.value)}
											value={initialCashDate}
											className="form-control"
										/>
									)}
								</td>
							</tr>
							{paymentsQty > 0 &&
								Array(paymentsQty)
									.fill('')
									.map((_, idx) => {
										return (
											<tr key={idx}>
												<td className="text-end">
													<strong>Cuota {idx + 1}:</strong>
												</td>
												<td align="center">
													<span>
														{((+paymentPerMonth * 100) / totalAmount).toFixed(
															2
														) + ' %'}
													</span>
												</td>
												<td align="center">
													<NumberFormat
														value={paymentPerMonth}
														displayType={'text'}
														thousandSeparator={true}
														prefix={'S/ '}
													/>
												</td>

												<td align="center">
													<span>{paymentsDates.at(idx)}</span>
												</td>
											</tr>
										);
									})}
							{isCash && (
								<tr>
									<td className="text-end">
										<strong>Entrega de bien fsico:</strong>
									</td>
									<td align="center">
										<span>
											{((+paymentPerMonth * 100) / totalAmount).toFixed(2) +
												' %'}
										</span>
									</td>
									<td align="center">
										<NumberFormat
											value={paymentPerMonth}
											displayType={'text'}
											thousandSeparator={true}
											prefix={'S/ '}
										/>
									</td>

									<td align="center">
										{modePDF ? (
											<span>{finalCashDate}</span>
										) : (
											<input
												type="date"
												className="form-control "
												onChange={(e) => setFinalCashDate(e.target.value)}
												value={finalCashDate}
											/>
										)}
									</td>
								</tr>
							)}

							{/* ULTIMA CUOTA 10% */}
							<tr>
								<td className="text-end">
									<strong>Formalizacin de venta:</strong>
								</td>
								<td align="center">
									{isCash ? (
										<span>10 %</span>
									) : (
										<NumberFormat
											value={(
												((totalAmount - initialCash) * 100) /
												totalAmount
											).toFixed(2)}
											displayType={'text'}
											thousandSeparator={true}
											suffix={' %'}
										/>
									)}
								</td>
								<td align="center">
									<NumberFormat
										value={
											isCash ? totalAmount * 0.1 : totalAmount - initialCash
										}
										displayType={'text'}
										thousandSeparator={true}
										prefix={'S/ '}
									/>
								</td>
								<td rowSpan={2}></td>
							</tr>
							{/* FILA TOTAL */}
							<tr>
								<td className="text-end">
									<strong>Total:</strong>
								</td>
								<td align="center">
									<span>100 %</span>
								</td>
								<td align="center">
									<NumberFormat
										value={totalAmount}
										displayType={'text'}
										thousandSeparator={true}
										prefix={'S/ '}
									/>
								</td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>
		</>
	);
};

export default FormPaymentList;
