import { createContext } from 'react';
import { BuildingsResponse } from '../../services/buildingservice';
import {
	CustomerObj,
	DptoObj,
	GarageObj,
	TypologyObj
} from '../../types/types';

interface FormContextType {
	modePDF: boolean;
	initialCashDate: string;
	finalCashDate: string;
	initialCash: number;
	paymentsQty: number;
	isCash: boolean;
	customer: CustomerObj | undefined;
	selectedBuilding: BuildingsResponse | undefined;
	selectedDpto: (DptoObj & { typology: TypologyObj }) | undefined;
	selectedGarage: GarageObj | undefined;
	discount: number;
	showGarages: boolean;

	resetQuote: () => void;
	setShowGarages: (arg: boolean) => void;
	setDiscount: (arg: number) => void;
	setSelectedGarage: (arg: GarageObj) => void;
	setSelectedDpto: (arg: DptoObj & { typology: TypologyObj }) => void;
	setSelectedBuilding: (arg: BuildingsResponse) => void;
	setCustomer: (arg: CustomerObj) => void;
	setIsCash: (arg: boolean) => void;
	setInitialCash: (arg: number) => void;
	setPaymentsQty: (arg: number) => void;
	setModePDF: (arg: boolean) => void;
	setInitialCashDate: (arg: string) => void;
	setFinalCashDate: (arg: string) => void;
}

export const FormContext = createContext<FormContextType | null>(null);
